import React,{Component} from 'react';
import { connect } from 'react-redux';
import ItemList from '../../component/ItemList/ItemList';
import './home.css';
import { Observable } from 'rxjs';
import * as listData from '../../data/CONTENTLISTINGPAGE-PAGE2.json';
import * as listDatas from '../../data/CONTENTLISTINGPAGE-PAGE3.json';
import back from '../../Slices/Back.png';
import search from '../../Slices/search.png';


class Home extends Component {

    constructor(props) {
        super(props);

        this.state = {
            error: false,
            hasMore: true,
            isLoading: false,
            users: [],
            query: '',
            filteredData: []
          };

       // Binds our scroll event handler
        window.onscroll = () => {
        const {
            loadUsers,
            state: {
            error,
            isLoading,
            hasMore,
            },
        } = this;

        // Bails early if:
        // * there's an error
        // * it's already loading
        // * there's nothing left to load
        if (error || isLoading || !hasMore) return;

        // Checks that the page has scrolled to the bottom
        if (
            window.innerHeight + document.documentElement.scrollTop
            === document.documentElement.offsetHeight
        ) {
            loadUsers();
        }
        };
    }

    loadUsers = () => {
        this.observable.subscribe((message)=> {
            //this.props.content
            console.log(message.default.page.contentItems.content);
            const moviesPoster = message.default.page.contentItems.content;
            moviesPoster.forEach(item => {
                 return this.state.users.push(item);
            });

            this.setState({
                hasMore: this.state.users.length === 54 ? false : true ,
                isLoading: false,
                users: [...this.state.users]
              });
        });
    }

     observable =  Observable.create(observer => {
        if(this.state.users.length === 40){
            observer.next(listDatas);
        }
        else{
            observer.next(listData);
        }
        observer.complete();
    });
        

    componentDidMount(){
        console.log(this.props.content);
        this.setState({
            users : this.props.content,
            filteredData: this.props.content
        })
    }

    handleInputChange = event => {
        const query = event.target.value;
    
        this.setState(prevState => {
          let users = prevState.users.filter(element => {
             if(query === ''){
                 return this.state.filteredData;
             }
            return element.name.toLowerCase().includes(query.toLowerCase());
          });

          if(query === ''){
            users = this.state.filteredData
            return {
                query,
                users
              };
          }
          else{
            return {
                query,
                users
              };
          }
        });
      };

    render(){

        return (
            <>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', backgroundColor: 'black', padding: '10px'}}>
                <div>
                  <img src={back} style={{height: '20px'}}/>
                </div>
                <div style={{color: 'white', height: '20px'}}>
                    Romantic Comedy
                </div>
                <div> 
                <input
                style={{borderWidth: '0', backgroundColor: 'black'}}
                value={this.state.query}
                onChange={this.handleInputChange}
                />
                <img src={search} style={{height: '20px'}}/>
            </div>
            </div>
          
            <div className="tiles">
                {this.state.users.length > 0 && this.state.users.map((data, index) => {
                    return (
                         <ItemList image={data.image} name={data.name} key={index} />
                    )
                })}
                {!this.state.hasMore &&
                <div></div>
                }
            </div>
            </>
        );
    }
}

const mapStateToProps  = (state) => {
    return{
       content : state.contentItems.content
    }
}

export default connect(mapStateToProps, null)(Home);